@import "mixin/baseImport";@import "_sprite/club_fun_factory_sprite";@import "mixin/clubhouse";//#wrap{//	&.club-house-fun-factory{//		.wrap-outer{//			.wrap-inner{background: url("/assets/images/club_house_fun_factory/bg_with_parents_fun_factory01.jpg") no-repeat 0 0;}//		}		//content		#container{}		#content{}		.content-wrap{			&__outer{}			&__inner{}			.swiper{padding: 160px 0 0;height: 768px;position: relative;box-sizing:border-box;				&-container{width: 100%;}				&-wrapper{}				&-controller{					.btn-swiper-prev{position: absolute;top: 325px;left: 10px;width: 44px;height: 72px;background: url("/assets/images/club_house_fun_factory/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 2;						&.swiper-button-disabled{opacity: 0;}					}					.btn-swiper-next{position: absolute;top: 325px;right: 10px;width: 44px;height: 72px;background: url("/assets/images/club_house_fun_factory/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 2;						&.swiper-button-disabled{opacity: 0;}					}				}				&-slide{padding: 22px 0 14px;z-index: 1;}			}			.clubhouse-fun-factory{				&__area{}				&__box{}				&__bg{}				&__items{width: 826px;height: 441px;padding-bottom: 25px;margin: 0 auto;display: flex;justify-content: center;flex-wrap: wrap;					&.length1{						.clubhouse-fun-factory__item{							&:nth-of-type(1){margin: 0 40px;}							&:nth-of-type(2){display: none;}							&:nth-of-type(3){display: none;}							&:nth-of-type(4){display: none;}							&:nth-of-type(5){display: none;}						}					}					&.length2{						.clubhouse-fun-factory__item{							&:nth-of-type(1){margin: 0 40px;}							&:nth-of-type(2){margin: 0 40px;}							&:nth-of-type(3){display: none;}							&:nth-of-type(4){display: none;}							&:nth-of-type(5){display: none;}						}					}					&.length3{						.clubhouse-fun-factory__item{							&:nth-of-type(1){margin-right: 40px;}							&:nth-of-type(2){margin-right: 40px;}							&:nth-of-type(3){margin-right: 40px;}							&:nth-of-type(4){display: none;}							&:nth-of-type(5){display: none;}						}					}					&.length4{background: url("/assets/images/club_house_fun_factory/bg_shelf_length04.png") no-repeat 50% 196px;width: 520px;						.clubhouse-fun-factory__item{							&:nth-of-type(1){margin: 0 15px;}							&:nth-of-type(2){margin: 0 15px;}							&:nth-of-type(3){margin: 27px 15px 0;}							&:nth-of-type(4){margin: 27px 15px 0;}							&:nth-of-type(5){display: none;}						}					}					&.length5{background: url("/assets/images/club_house_fun_factory/bg_shelf_length05.png") no-repeat 50% 196px;						.clubhouse-fun-factory__item{							&:nth-of-type(1){margin-right: 34px;}							&:nth-of-type(2){margin-right: 34px;}							&:nth-of-type(3){margin-right: 34px;}							&:nth-of-type(4){margin: 27px 34px 0 0;}							&:nth-of-type(5){margin: 27px 0 0;}						}					}				}				&__item{position: relative;width: 194px;height: 207px;display: block;transform: scale(1);background: no-repeat 0 100%;transition: transform 0.3s;					@for $i from 1 through 4{						&.bg-color0#{$i}{							&:not(.this-week):not(.vacation):not(.disabled){								&.active{background-image: url("/assets/images/club_house_fun_factory/bg_item0#{$i}_active.png") !important;}							}							&.default,							&.disabled{background-image: url("/assets/images/club_house_fun_factory/bg_item0#{$i}_default.png");								&:before{background-image: url("/assets/images/club_house_fun_factory/icon_ribbon0#{$i}.png")}							}							&.this-week{background-image: url("/assets/images/club_house_fun_factory/bg_item0#{$i}_this_week.png");}							&.complete{background-image: url("/assets/images/club_house_fun_factory/bg_item0#{$i}_default.png");}							.complete-box{								.icon{									&-star{background-image: url("/assets/images/club_house_fun_factory/icon_star0#{$i}.png");										&.active{background-image: url("/assets/images/club_house_fun_factory/icon_star_active.png")}									}								}							}						}					}					&.active{						&:not(.this-week):not(.vacation):not(.disabled){							.complete-box{display: block;top: 44px;}							.img{opacity: 1;}						}					}					&.default{						.complete-box{display: block;top: 66px;}					}					&.disabled{						&:before{content:"";position: absolute;top: 22px;left: 40px;width: 115px;height: 104px;background: no-repeat 0 0;}						.tit-week{opacity: 0.3;}						.complete-box{display: none;}					}					&.complete{						.complete-box{display: block;top: 66px;}					}					&.this-week{						.btn:before{content: "";position: absolute;top: -15px;left: 50%;width: 261px;height: 238px;margin-left: -130px;animation: this-week-icon 1s steps(2) infinite forwards;background: url("/assets/images/sprite/club_house_fun_factory_this_week.png") no-repeat 0 0;z-index: 1; }						.img{top: 55px;height: 95px;opacity: 1;							&:before{top: 35px;}							&:after{top: 35px;}						}						.tit-week{color: #fff509;}						.complete-box{display: block;top: 10px;transform: scale(0.8)}					}					&.vacation{background-image: url("/assets/images/club_house_fun_factory/bg_vacation01.png") !important;						.tit-week{display: none;}						.img{display: none;}						.complete-box{display: none;}					}					.complete-box{display: none;position: absolute;left: 50%;width: 120px;height: 48px;margin-left: -60px;z-index: -1;						.icon{							&:nth-of-type(1){float: left;}							&:nth-of-type(2){float: right;}						}					}					.tit-week{position: absolute;bottom: 32px;left: 0;width: 100%;height: 26px;display: flex;font-family: $nsroundecp;font-size: 30px;color: #fff;letter-spacing: 1px;align-content: center;justify-content: center;						.number-box{margin: 3px 0 0 10px;}					}					.img{position: absolute;top: 79px;left: -5px;overflow: hidden;width: 194px;height: 48px;opacity: 0;transition: all 0.3s;						&:before{content:"";overflow: hidden;position: absolute;top: 30px;left: 86px;width: 10px;height: 10px;border-radius: 10px;background-color: #4974cb;transition: all 0.3s}						&:after{content:"";overflow: hidden;position: absolute;top: 30px;right: 80px;width: 10px;height: 10px;border-radius: 10px;background-color: #4974cb;transition: all 0.3s;}					}					.btn{overflow: visible;position: relative;display: block;width: 100%;height: 100%;}					.icon{						&-star{display: inline-block;width: 50px;height: 48px;background: no-repeat 0 0;}					}				}			}		}	//}//}/*/deep/ #wrap{	&.club-fun-factory{		@extend %swiper-pagination;	}}*/